
import React, { Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';

import s from './Slick.css';
import SplashImgUrl1 from '../../assets/images/splash1.jpg';
import SplashImgUrl2 from '../../assets/images/splash2.jpg';
import SplashImgUrl3 from '../../assets/images/splash3.jpg';
import SplashImgUrl4 from '../../assets/images/splash4.jpg';
import SplashImgUrl5 from '../../assets/images/splash5.jpg';

const SPLASH_IMAGES = [
  SplashImgUrl1, SplashImgUrl2, SplashImgUrl3, SplashImgUrl4, SplashImgUrl5,
];

class Splash extends Component {

  constructor(props) {
    super(props);
    this.state = {
      currentIndex: 0,
    };
  }

  componentDidMount() {
    this.timer = setInterval(() => {
      this.setState({
        currentIndex: (this.state.currentIndex + 1) % SPLASH_IMAGES.length,
      });
    }, 15000);
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  render() {
    const currentIndex = this.state.currentIndex;
    return (
      <div className={s.container}>
        {
          SPLASH_IMAGES.map((image, index) => (<div
            key={image}
            className={s.image}
            style={{
              backgroundImage: `url(${image})`,
              opacity: index === currentIndex ? 1 : 0,
            }}
          />))
        }
      </div>
    );
  }
}

export default withStyles(s)(Splash);



