
import React from 'react';
import Layout from '../../components/Layout';
import Page from '../../components/Page';

export default {

  path: '/recruiter',

  async action() {
    const data = await require.ensure([], require => require('./recruiter.md'), 'recruiter');

    return {
      title: data.title,
      component: <Layout><Page {...data} /></Layout>,
    };
  },

};



