
import React from 'react';
import Layout from '../../components/Layout';
import Page from '../../components/Page';

export default {

  path: '/terms',

  async action() {
    const data = await require.ensure([], require => require('./terms.md'), 'terms');

    return {
      title: data.title,
      component: <Layout><Page {...data} /></Layout>,
    };
  },

};



