
import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import normalizeCss from 'normalize.css';
import IconButton from 'material-ui/IconButton';
import { Toolbar, ToolbarGroup, ToolbarTitle } from 'material-ui/Toolbar';

import s from './Home.css';
import Sidebar from '../../components/Sidebar';
import history from '../../history';

class Home extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      docked: false,
      isOpen: false,
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.updatedDimensions);
    this.updatedDimensions();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updatedDimensions);
  }

  updatedDimensions = () => {
    const docked = window.innerWidth >= 768;
    if (docked !== this.state.docked) {
      this.setState({
        docked,
        isOpen: docked,
      });
    }
  }

  sidebarToggle = () => this.setState({ isOpen: !this.state.isOpen });

  sidebarClose = () => this.setState({ isOpen: false });

  logout = () => {
    history.push('/');
  }

  render() {
    const { docked, isOpen } = this.state;
    const leftSpace = this.state.isOpen && this.state.docked ? '256px' : 0;

    return (
      <div>
        <Sidebar
          docked={docked}
          isOpen={isOpen}
          close={this.sidebarClose}
        />

        <div>
          <Toolbar
            style={{ paddingLeft: leftSpace }}
            className={s.toolbar}
          >
            <ToolbarGroup>
              {
                docked ? '' :
                (<IconButton
                  iconClassName="fa fa-bars"
                  onTouchTap={this.sidebarToggle}
                />)
              }
              <ToolbarTitle
                text="Title"
                className={s.title}
              />
            </ToolbarGroup>
            <ToolbarGroup>
              <IconButton
                iconClassName="fa fa-sign-out"
                onTouchTap={this.logout}
              />
            </ToolbarGroup>
          </Toolbar>

          <div
            style={{ marginLeft: leftSpace }}
            className={s.container}
          >
            content...
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(normalizeCss, s)(Home);



