
import React from 'react';
import Layout from '../../components/Layout';
import Page from '../../components/Page';

export default {

  path: '/jobseeker',

  async action() {
    const data = await require.ensure([], require => require('./jobseeker.md'), 'jobseeker');

    return {
      title: data.title,
      component: <Layout><Page {...data} /></Layout>,
    };
  },

};



