
import React from 'react';
import PropTypes from 'prop-types';
import Drawer from 'material-ui/Drawer';
import { List, ListItem } from 'material-ui/List';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Sidebar.css';
import TitleImgUrl from '../../assets/images/title.png';

class Sidebar extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1,
    };

    this.recruiterMenus = [
      { id: 1, label: 'Find Talent', icon: 'fa-search' },
      { id: 2, label: 'Applications', icon: 'fa-id-badge' },
      { id: 3, label: 'Connections', icon: 'fa-handshake-o' },
      { id: 4, label: 'My Shortlist', icon: 'fa-tags' },
      { id: 5, label: 'Messages', icon: 'fa-envelope-o' },
      { id: 6, label: 'Add or Edit Jobs', icon: 'fa-briefcase' },
      { id: 7, label: 'Credit', icon: 'fa-credit-card' },
      { id: 8, label: 'Change Password', icon: 'fa-key' },
      { id: 9, label: 'Help', icon: 'fa-question-circle-o' },
    ];
  }

  onRequestChange = () => {
    if (!this.props.docked) {
      this.props.close();
    }
  }

  onClickItem = (currentPage) => {
    this.setState({ currentPage });
  }

  render() {
    const { docked, isOpen } = this.props;
    const currentPage = this.state.currentPage;
    return (
      <Drawer
        docked={docked}
        open={isOpen}
        containerStyle={
          docked ? { boxShadow: 'inset -8px 0 8px -8px rgba(0, 0, 0, 0.5)' } : {}
        }
        containerClassName={s.root}
        onRequestChange={this.onRequestChange}
      >

        <div className={s.title}>
          <img src={TitleImgUrl} alt="" />
        </div>
        {/* <hr /> */}
        <List>
          {
            this.recruiterMenus.map((info) => {
              const active = info.id === currentPage ? s.active : '';
              return (
                <ListItem
                  key={info.id}
                  className={`${s.itemRoot} ${active}`}
                  onClick={() => this.onClickItem(info.id)}
                >
                  <i className={`fa ${info.icon}`} />
                  { info.label }
                  {
                    docked && info.id === currentPage ?
                      <span className={s.arrow} /> : ''
                  }
                </ListItem>
              );
            })
          }
        </List>

      </Drawer>
    );
  }
}

Sidebar.propTypes = {
  docked: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  close: PropTypes.func.isRequired,
};

export default withStyles(s)(Sidebar);



