
import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Landing.css';
import Slick from '../../components/Slick';
import GooglePlayImgUrl from '../../assets/images/googleplay-button.svg';
import iTunesImgUrl from '../../assets/images/itunes-button.svg';

class Landing extends React.Component {

  render() {
    return (
      <div>
        <Slick />
        <div className={s.content}>
          <div className={s.title}>
            Recruitment where first impression count
          </div>
          <div>
            <a href="/register" className={s.register}>
              <span>{"I'm a"}</span><br />
              <span>Job Seeker</span>
            </a>
            <a href="/register" className={s.register}>
              <span>{"I'm a"}</span><br />
              <span>Recruiter</span>
            </a>
          </div>
          <a href="">
            <img src={GooglePlayImgUrl} alt="" className={s.appButton} />
          </a>
          <br />
          <a href="">
            <img src={iTunesImgUrl} alt="" className={s.appButton} />
          </a>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Landing);



